html, body, #root {
    height: 100%;
}

body {
    font-family: 'Segoe UI', 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

input, select {
    outline-color: var(--light-theme-primary-color);
    background-color: #f4f4f6;
    border-color: #d3d4dc;
}

input[type=checkbox] {
    border-radius: 4px;
}

.errorBorder {
    border: 2px solid red !important;
}

input[type=checkbox].errorBorder {
    -moz-appearance: none;
    -webkit-appearance: none;
    -o-appearance: none;
    padding: 0;
}

.link {
    color: var(--light-theme-secondary-color);
    text-decoration: none;
}

.fs-875em {
    font-size: 0.875em;
}

.fw-500 {
    font-weight: 500;
}

.fw-bold {
    font-weight: bold;
}

.bgc-primary {
    background-color: var(--light-theme-primary-color);
}

.app-BG-Color {
    background-color: var(--light-theme-bg-color);
}