.phoneNumberContainer {
	display        : flex;
	gap            : 0.5em;
	align-items    : flex-end;
	justify-content: flex-start;

	.phoneNumberDropdown {
		flex       : 1;
		flex-shrink: 1;

		select {
			padding-inline: 0.5em !important;
		}
		
		&.errorBorder {
			select {
				border: 2px solid red;
			}
		}
	}

	.phoneNumberEntry {
		flex       : 3;
		flex-shrink: 1;
		
		&.errorBorder {
			input {
				border: 2px solid red !important;
			}
		}
	}

	@media screen and (min-width:320px) {
		.phoneNumberEntry {
			input[type=text] {
				width: 100%;
			}
		}
	}
}