.bodyContainer {
	display           : grid;
	grid-template-rows: min-content 1fr;

	main {
		height     : inherit;
		display    : flex;
		align-items: center;

		.firstHalf {
			align-self: stretch;
			padding   : 12rem 25rem 0 12rem;

			@media screen and (min-width: 300px) {
				padding: 1rem;
			}

			//iPad mini
			@media screen and (min-width: 768px) {
				padding: 14rem 0rem 0 2rem;
			}
			
			//iPad Pro
			@media screen and (min-width: 1024px) {
				padding: 26rem 0rem 0 2rem;
			}

			//iPad
			@media screen and (min-width: 810px) {
				padding: 14rem 0rem 0 3rem;
			}

			@media screen and (min-width: 1280px) {
				padding: 8rem 3rem 0 4rem;
			}

			@media screen and (min-width: 1440px) {
				padding: 10rem 5rem 0 5rem;
			}

			@media screen and (min-width: 1920px) {
				padding: 15rem 15rem 0 7rem;
			}

			.campaignHeading {
				font-size: 2em;

				@media screen and (min-width: 1200px) {
					font-size: 5rem;
				}
			}

			.campaignSubheading {
				font-size: 1em;

				@media screen and (min-width: 1200px) {
					font-size: 1.4rem;
				}
			}
		}

		.secondHalf {
			@media screen and (min-width: 810px) {
				padding       : 0 2em;
			}
			@media screen and (min-width: 1920px) {
				padding       : 0 7em;
			}

			.loginContainer {
				border-radius: 18px;

				h4 {
					font-size: 1em;
				}

				.tooltipIcon {
					font-size  : 0.9em;
					display    : flex;
					align-items: center;
					gap        : 5px;
				}

				.signUpLinkButton {
					height      : 3.2em;
					width       : 90%;
					justify-self: center;
				}

				.signUpLink {
					font-weight    : bold;
					text-decoration: none;
					color          : white;
					letter-spacing : 1px;
				}
			}
		}
	}
}